<template>
  <div class="vehicle-item" @click="clickVehicle">
    <div class="img">
      <img src="/static/img/hongyanbg.jpg" alt="">
    </div>
    <div class="infos">
      <h4>{{vehicle.vehicle_license_province}} {{vehicle.vehicle_license_number}}</h4>
      <p>姓名: {{vehicle.uname}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'VehicleItem',
  props: {
    vehicle: Object
  },
  methods: {
    clickVehicle () {
      this.$parent.$emit('clickOneVehicle', this.vehicle)
    }
  }
}
</script>

<style lang="less" scoped>
  .vehicle-item{
    display: flex;
    padding: .6rem;
    margin-top: .6rem;
    background-color: #fff;
    .img{
      width: 3rem;
      img{
        width: 100%;
      }
    }
    .infos{
      flex: 1;
      padding-left: 1rem;
      h4{
        margin: 0;
      }
      p{
        margin: 0;
        font-size: 14px;
        color: #999;
      }
    }
  }
</style>



