<template>
  <div class="detail">
    <span>姓名: {{vehicleInfo.uname}}</span>
    <span>品牌：{{vehicleInfo.vehicle_brand}}</span>
    <span>角色：{{vehicleInfo.role === 1 ? '使用者' : '拥有者'}}</span>
    <span>手机：{{vehicleInfo.pnum}}</span>
    <span>车架号：{{vehicleInfo.vin}}</span>
    <span style="color: red; text-align: center;" @click="removeVehicle">删除该车辆</span>
  </div>
</template>

<script>
import {removeVehicle} from '../../../api/home'
import bus from '../../../assets/js/bus'
export default {
  name: 'VehicleDetail',
  props: {
    vehicleInfo: ''
  },
  data () {
    return {
      editVehicleState: 0
    }
  },
  methods: {
    removeVehicle () {
      removeVehicle(this.vehicleInfo.customer_id)
        .then(data => {
          bus.$emit('remove')
          this.$toast(data.errormsg || '删除成功')
        })
    },
    editVehicle () {
    }
  }
}
</script>

<style lang="less" scoped>
  .detail{
    span{
      padding: 1rem .5rem;
      display: block;
      font-size: 14px;
      &:nth-child(2n) {
        background-color: #eee;
      }
    }
  }
</style>



