<template>
  <div class="ui-wrap m-wrap">
    <app-header :title="'我的车辆列表'" showBack="true"></app-header>
    <div class="ui-title">
      <a href="javascript:void (0)" style="color: #333;" @click="addcar">添加车辆</a>
    </div>
    <div class="vehicle-wrap">
      <vehicle-item
        v-for="(item, index) in vehicleList"
        :key="index"
        :vehicle="item"
      ></vehicle-item>
    </div>
    <mt-popup
      style="border-radius: 10px; width: 80%;"
      v-model="popupVisible"
      pop-transition="popup-fade">
      <vehicle-detail :vehicleInfo="vehicleOne"></vehicle-detail>
    </mt-popup>
    <mt-popup
      style="height: 100%; width: 100%; z-index: 1;"
      v-model="addVehicleViewState">
      <vehicle-add></vehicle-add>
    </mt-popup>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import VehicleItem from './vehicle-item.vue'
import VehicleDetail from './vehicle-detail.vue'
import VehicleAdd from './vehicle-add.vue'
import bus from '../../../assets/js/bus'
import {getVehicleList} from '../../../api/home'
export default {
  name: 'vehicle',
  data () {
    return {
      popupVisible: false,
      addVehicleViewState: false,
      vehicleList: [],
      vehicleOne: ''
    }
  },
  created () {
    this.getData()
  },
  mounted () {
    this.$on('clickOneVehicle', (vehicle) => {
      this.$set(this, 'vehicleOne', vehicle)
      this.$set(this, 'popupVisible', true)
    })
    bus.$on('addVehicleSuccess', () => {
      this.addVehicleViewState = false
      this.getData()
    })
    bus.$on('remove', () => {
      this.$set(this, 'popupVisible', false)
      this.getData()
    })
    bus.$on('clickCloseModal', () => {
      this.addVehicleViewState = false
    })
  },
  methods: {
    getData () {
      getVehicleList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'vehicleList', data.data.list)
          }
        })
    },
    addcar () {
      this.addVehicleViewState = true
    }
  },
  components: {
    AppHeader,
    VehicleItem,
    VehicleDetail,
    VehicleAdd
  }
}
</script>

<style lang="less" scoped>
  .vehicle-wrap{
  }
</style>



