<template>
  <div class="add-wrap">
    <app-header
      title="添加车辆"
      showBack="false"
      close="true"
    ></app-header>
    <div class="add-main">
      <mt-field label="VIN号" placeholder="请输入VIN后八位号,字母请大写" v-model="vehicle.vin"></mt-field>
      <mt-field label="姓名" placeholder="请输入姓名" type="email" v-model="vehicle.uname"></mt-field>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">角色</span></div>
          <div class="mint-cell-value">
            <label>使用者
              <input type="radio" name="role" v-model="vehicle.role" value="1">
            </label>
            <label>
              拥有者
              <input type="radio" name="role" v-model="vehicle.role" value="2">
            </label>
          </div>
        </div>
      </a>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">车辆所属</span></div>
          <div class="mint-cell-value">
            <select v-model="vehicle.vehicle_license_province" style="width: 80%;">
              <option value="京">京</option>
              <option value="津">津</option>
              <option value="沪">沪</option>
              <option value="渝">渝</option>
              <option value="冀">冀</option>
              <option value="晋">晋</option>
              <option value="辽">辽</option>
              <option value="吉">吉</option>
              <option value="黑">黑</option>
              <option value="苏">苏</option>
              <option value="浙">浙</option>
              <option value="皖">皖</option>
              <option value="闽">闽</option>
              <option value="赣">赣</option>
              <option value="鲁">鲁</option>
              <option value="豫">豫</option>
              <option value="鄂">鄂</option>
              <option value="湘">湘</option>
              <option value="粤">粤</option>
              <option value="琼">琼</option>
              <option value="川">川</option>
              <option value="贵">贵</option>
              <option value="云">云</option>
              <option value="陕">陕</option>
              <option value="甘">甘</option>
              <option value="青">青</option>
              <option value="藏">藏</option>
              <option value="桂">桂</option>
              <option value="蒙">蒙</option>
              <option value="宁">宁</option>
              <option value="新">新</option>
            </select>
          </div>
        </div>
      </a>
      <mt-field label="车牌号" placeholder="请输入车牌号" type="email" v-model="vehicle.vehicle_license_number"></mt-field>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">车辆类别</span></div>
          <div class="mint-cell-value">
            <select v-model="vehicle.type" style="width: 80%;">
              <option value="自卸车">自卸车</option>
              <option value="牵引车">牵引车</option>
              <option value="载货车">载货车</option>
              <option value="专用车">专用车</option>
              <option value="其他">其他</option>
            </select>
          </div>
        </div>
      </a>
      <a href="javascript:void(0)" class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"><span class="mint-cell-title">车辆品牌</span></div>
          <div class="mint-cell-value">
            <select v-model="vehicle.vehicle_brand" style="width: 80%;">
              <option value="红岩">红岩</option>
              <option value="解放">解放</option>
              <option value="东风">东风</option>
              <option value="重汽">重汽</option>
              <option value="陕汽">陕汽</option>
              <option value="欧曼">欧曼</option>
              <option value="江淮">江淮</option>
              <option value="大运">大运</option>
              <option value="华菱">华菱</option>
              <option value="北奔">北奔</option>
              <option value="其他">其他</option>
            </select>
          </div>
        </div>
      </a>
      <mt-button type="primary" plain size="large" style="margin-top: 3rem;" @click="addVehicle">添加车辆</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import bus from '../../../assets/js/bus'
import {addVehicleFn} from '../../../api/home'
export default {
  name: 'Vehicle-Add',
  data () {
    return {
      vehicle: {
        vin: '',
        uname: '',
        role: '',
        vehicle_license_province: '',
        vehicle_license_number: '',
        type: '',
        vehicle_brand: ''
      }
    }
  },
  mounted () {
  },
  methods: {
    addVehicle () {
      addVehicleFn(this.vehicle)
        .then(data => {
          if (data.state) {
            this.$toast('添加成功')
            bus.$emit('addVehicleSuccess')
          } else {
            this.$toast(data.errormsg || '添加失败')
          }
        })
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .add-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .add-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
    }
  }
</style>



